<template>
    <div class="home dark-white-bg">
        <el-row :class="{home: true, 'dark-white-bg': true}">
            <el-col :span="4" :class="{menu: true, 'light-gray-bg': true}">
                <component :is="menu" :nivel="nivel" @opcao="principal"></component>
            </el-col>
            <el-col :span="20" :class="{content: true}">
                <component :is="opcao"></component>
            </el-col>
        </el-row>
    </div>
</template>
<script>
    import Menu from './Menu';
    import Pedidos from './Pedidos';

    export default {
        data() {
            return {
                menu: Menu,
                nivel: window.sessionStorage.getItem('nivel'),
                opcao: ''
            }
        },
        methods: {
            principal(op) {
                switch(op[0]) {
                    case 'pedidos':
                        this.opcao = Pedidos;
                        break;
                    case 'estoque':
                        this.opcao = '';
                        break;
                    case 'opcoes':
                        this.opcao = '';
                        break;
                    case 'lanches':
                        this.opcao = '';
                        break;
                    case 'refeicao':
                        this.opcao = '';
                        break;
                }
            }
        },
        components: {
            Menu,
            Pedidos
        }
    }
</script>
<style scoped>
    .home {
        height: 100vh;
        width: 100vw;
    }
    el-row {
        background-color: orange;
    }
    .menu {
        height: 100vh;
    }

    #t {
        background-color: purple;
    }

    @media (max-width: 768px) {
        .menu {
            display: flex;
            width: 100vw;
            height: 15vh;
            justify-content: center;
            order: 2;
        }
        .content {
            width: 100vw;
            height: 85vh;

        }
        .home > * {
            display: flex;
            flex-direction: column;
        }
    }
</style>


