<template>
    <!--div class="container">
        <!-- 2 PAI, 3 CANTINA -->
        <el-row :class="{items: true, 'silver-bg': true}">
            <el-col :class="{item: true, 'silver-bg': true, 'xs-light-black-bg-h': true, 'light-gray': true}">
                <div class="item" @click="display(menu.op1.link, $event)"><i class="material-icons">{{menu.op1.icon}}</i><p>{{menu.op1.texto}}</p></div>
            </el-col>
            <el-col @click="display(menu.op2.link)" :class="{item: true, 'silver-bg': true, 'xs-light-black-bg-h': true, 'light-gray': true}">
                <div class="item" @click="display(menu.op2.link, $event)"><i class="material-icons">{{menu.op2.icon}}</i><p>{{menu.op2.texto}}</p></div>
            </el-col>
            <el-col @click="display(menu.op3.link)" :class="{item: true, 'silver-bg': true, 'xs-light-black-bg-h': true, 'light-gray': true}">
                <div class="item" @click="display(menu.op3.link, $event)"><i class="material-icons">{{menu.op3.icon}}</i><p>{{menu.op3.texto}}</p></div>
            </el-col>
        </el-row>
    <!--/div-->
</template>
<script>
    export default {
        props: ['nivel'],
        data() {
            return {
                menu: '',
                cantina: {
                    op1: {
                        texto: 'PEDIDOS',
                        link: 'pedidos',
                        icon: 'receipt'
                    },
                    op2: {
                        texto: 'ESTOQUE',
                        link: 'estoque',
                        icon: 'store_mall_directory'
                    },
                    op3: {
                        texto: 'OPÇÕES',
                        link: 'opcoes',
                        icon: 'settings'
                    }
                },
                pais: {
                    op1: {
                        texto: 'LANCHES',
                        link: 'lanches',
                        icon: 'local_pizza'
                    },
                    op2: {
                        texto: 'REFEIÇÃO',
                        link: 'refeicao',
                        icon: 'restaurant'
                    },
                    op3: {
                        texto: 'OPÇÕES',
                        link: 'opcoes',
                        icon: 'settings'
                    }
                }
            }
        },
        methods: {
            display(op, event) {
                let element = event.target;
                if (event.target.localName !== 'div') {
                    element = event.target.parentElement;
                }
                let allElements = document.getElementsByClassName('light-black-bg');
                [].forEach.call(allElements, function(el) {
                    el.classList.remove('light-black-bg');
                });
                
                element.classList.add('light-black-bg');
                this.$emit('opcao', [op]);
            }
        },
        created() {
            if (this.nivel == 2) {
                this.menu = this.pais;
            } else if (this.nivel == 3) {
                this.menu = this.cantina;
            } else {
                this.menu = 'n tem nada';
            }
        }
    }
</script>
<style scoped>
    .items {
        display: flex;
        flex-direction: column;
        height: 100vh;
        justify-content: center;
        align-content: flex-end;
    }

    .item {
        display: flex;
        height: 20vh;
        width: 100%;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }

    .item > i {
        font-size: 2rem;
    }
    .item {
        font-size: 1rem;
        font-family: 'Ubuntu', sans-serif;
    }
    .item:hover {
        cursor: pointer;
    }

    @media screen and (min-width: 769px) {
        .item:not(:last-child) {
            margin-bottom: 2vh;
        }
    }

    .container {
        height: inherit;
        background-color: #3366aa;
        align-items: center;
    }
    @media screen and (max-width: 768px) {
        .container {
            flex-direction: column-reverse;
        }
        .item {
            height: 15vh;
            width: 32vw;
        }
        .item:not(:last-child) {
            margin-right: 2vw;
        }
        .items {
            flex-direction: row;
            height: auto;
        }
    }
    @media screen and (max-width: 300px) {
        .item {
            font-size: 0.75rem;
        }
    }
</style>


